'use strict';

import React from 'react';
import {render} from 'react-dom';

import PercentCircle from './components/percentcircle'


class SystemLoadStats extends React.Component{

    render() {

        return (
            <div>
                <PercentCircle percentage="23" name="CPU"/>
                <div style={{height : '5px'}} />
                <PercentCircle percentage="65" name="Fabric Attached Memory"/>
                <div style={{height : '5px'}} />
                <PercentCircle percentage="0" name="Fabric"/>
            </div>
        );

    }//render

}//class


render(<SystemLoadStats />, document.getElementById('SystemLoadStats'));
//render(<PercentCircle percentage="23"/>, document.getElementById('PercentCircleCmp'));



