import React from 'react';
import {render} from 'react-dom';

//ommiting extension of the file. Webpack config will figure it out by looking for .css or .scss
import css from './css/percentcircle'
import styles from './css/percentcircle'

class PercentCircle extends React.Component {

    constructor(props){
        super(props);
    }


  render () {
    var radius = 80;
    var progress_fill = radius * 2 * 3.14159;
    var circle_fill_offset = progress_fill * this.props.percentage / 100;

    return (
        <div className={css.container}>
            <svg width="180" height="200">
                <circle className={[css.progress_inactive, css.circle].join(' ')}/>
                <circle className={[css.progress, css.circle].join(' ')}
                        strokeDasharray={progress_fill}
                        strokeDashoffset={circle_fill_offset} />

                <text x="50%" y="40%" className={css.precent_text}>{this.props.percentage}</text>
                <text x="50%" y="52%" className={css.precent_symbol}>%</text>
            </svg>
            <span className={css.dsc}>{this.props.name}</span>
        </div>
    );
  }//render
}//class


PercentCircle.defaultProps = {
    percentage : 0,
    name : ""
}


export default PercentCircle;



